import React, {Fragment} from "react"
import { Link } from "gatsby"
import { Page, StatsCard, Grid, Alert } from "tabler-react"
import axios from "axios"

import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"


const MStatsCard = ({ label, total, movement }) => (
  <Grid.Col lg={2} md={4} sm={6} xs={12}>
    <StatsCard layout={1} movement={ movement } total={total} label={label} />
  </Grid.Col>
)

const Dashboard = ({ board }) => {
  var items;
  console.log( board );
  if (board && board.length > 0)
    items = board.map(({ type, ...props }, index) => {
      console.log( type );
      if (type=="StatsCard")
        return (<MStatsCard key={index} {...props} />)
    })
    console.log(items);
  if (items)
    return (<Grid.Row>{items}</Grid.Row>);
  else
    return (<Grid.Row></Grid.Row>);
}

class IndexPage extends React.Component {
  
  fetchOnline(){    
    var _this = this;
    
      axios
        .get(`https://ourhealthcarehk.github.io/api/dashboard.json`)
        .then(({ data }) => {
          
          
          if (!data)
            return;
          
          _this.setState({ dashboard: data });
                    
        })
        .catch(error => {
          _this.setState({ loading: false, error })
        })
  }
  
  constructor() {
    super()
    this.state = { dashboard:[] };
  }  
  
	componentDidMount() {
    this.fetchOnline();
	}
  
  render() {
    return (
  <Layout>
    <SEO title="主頁" />
    <Page.Content title="主頁">
    <Alert type="primary">你好！我們正在建設這個網站，部分資料可能有延遲或錯漏。</Alert>
    <Alert type="secondary">你可以在上面的工具欄選取你想瀏覽的資料。</Alert>
    <div>
      <Dashboard board={this.state.dashboard} />
    </div>
    </Page.Content>
  </Layout>
);
    
  }
}

export default IndexPage
